<template>
  <div>
    <LoadingView :active="isLoading"></LoadingView>
    <div class="text-end mt-4">
      <button class="btn btn-primary" @click="openCouponModal(true)">
        建立新的優惠券
      </button>
    </div>
    <table class="table mt-4">
      <thead>
      <tr>
        <th>名稱</th>
        <th>折扣百分比</th>
        <th>到期日</th>
        <th>是否啟用</th>
        <th>編輯</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="coupon in coupons" :key="coupon.id">
        <td>{{ coupon.title }}</td>
        <td>{{ coupon.percent }}%</td>
        <td>{{ this.$filters.date(coupon.due_date) }}</td>
        <td>
          <span class="text-success" v-if="coupon.is_enabled">啟用</span>
          <span v-else class="text-muted">未起用</span>
        </td>
        <td>
          <div class="btn-group">
            <button class="btn btn-outline-primary btn-sm" @click="openCouponModal(false, coupon)">編輯</button>
            <button class="btn btn-outline-danger btn-sm" @click="openDelCouponModal(coupon)">刪除</button>
          </div>
        </td>
      </tr>
      </tbody>
    </table>
    <PaginationView :pages="pagination" @emit-pages="getCoupons"></PaginationView>
    <couponModal ref="couponModal" :coupon="tempCoupon" @update-coupon="updateCoupon"></couponModal>
    <delModal ref="delModal" :item="tempCoupon" @del-item="delCoupon"></delModal>
    <!-- <couponModal :coupon="tempCoupon" ref="couponModal"
    @update-coupon="updateCoupon"/>
    <DelModal :item="tempCoupon" ref="delModal" @del-item="delCoupon"/> -->
  </div>
</template>

<script>
import couponModal from '@/components/CouponModal.vue'
import delModal from '@/components/DelModal.vue'
import PaginationView from '@/components/PaginationView.vue'
export default {
  data () {
    return {
      coupons: {},
      pagination: {},
      currentPage: 1,
      tempCoupon: {
        title: '',
        is_enabled: 0,
        percent: 100,
        code: ''
      },
      isLoading: false,
      isNew: false
    }
  },
  components: {
    couponModal,
    delModal,
    PaginationView
  },
  methods: {
    getCoupons (currentPage = 1) {
      this.currentPage = currentPage
      this.isLoading = true
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/coupons?page=${currentPage}`
      this.$http.get(url)
        .then((res) => {
          console.log(res)
          this.coupons = res.data.coupons
          this.pagination = res.data.pagination
          this.isLoading = false
        })
    },
    openCouponModal (isNew, item) {
      this.isNew = isNew
      if (this.isNew) {
        this.tempCoupon = {
          due_date: new Date().getTime() / 1000,
          is_enabled: 0
        }
      } else {
        this.tempCoupon = { ...item }
      }
      const couponCompent = this.$refs.couponModal
      couponCompent.showModal()
    },
    openDelCouponModal (item) {
      this.tempCoupon = { ...item }
      this.$refs.delModal.showModal()
    },
    updateCoupon (tempCoupon) {
      if (this.isNew) {
        const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/coupon`
        this.$http.post(url, { data: tempCoupon })
          .then((res) => {
            console.log(res, tempCoupon)
            this.$httpMessageState(res, '新增優惠券')
            this.getCoupons()
            this.$refs.couponModal.hideModal()
          })
      } else {
        const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/coupon/${this.tempCoupon.id}`
        this.$http.put(url, { data: this.tempCoupon })
          .then((res) => {
            console.log(res)
            this.$httpMessageState(res, '修改優惠券')
            this.getCoupons()
            this.$refs.couponModal.hideModal()
          })
      }
    },
    delCoupon () {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/coupon/${this.tempCoupon.id}`
      this.isLoading = true
      this.$http.delete(url)
        .then((res) => {
          console.log(res, this.tempCoupon)
          this.$httpMessageState(res, '刪除優惠券')
          this.$refs.delModal.hideModal()
          this.getCoupons()
        })
    }
  },
  created () {
    this.getCoupons()
  }
}
</script>
